body {
  padding-left: 5%;
  padding-right: 5%;
  font-size: 12px;
  position: relative;
}

h1 {
  font-size: 24px;
}

h2 {
  font-size: 16px;
}

.cat-info {
  gap: 20px;
  display: flex;
}

.cat-info-vis {
  visibility: hidden;
}

.loader {
  width: 8px;
  height: 48px;
  color: #000;
  box-sizing: border-box;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  animation: animloader .6s linear infinite;
  display: block;
  position: relative;
}

@keyframes animloader {
  0% {
    box-shadow: 20px -10px, 40px 10px, 60px 0;
  }

  25% {
    box-shadow: 20px 0, 40px 0, 60px 10px;
  }

  50% {
    box-shadow: 20px 10px, 40px -10px, 60px 0;
  }

  75% {
    box-shadow: 20px 0, 40px 0, 60px -10px;
  }

  100% {
    box-shadow: 20px -10px, 40px 10px, 60px 0;
  }
}

.breed-select {
  width: 300px;
}

